html {
  height: 100%;
  width: 100%;
  background-color:rgb(219, 102, 60);
}
div.app {
  height: 100%;
  width: 100%;
}

div.postbox {
  position: relative;
  left: 17.5vw;
  width: 65vw;
  height: 15vh;
  background-color: rgb(166, 200, 221);
  border: 0.75vh solid black;
  margin: 0 0 2vh;
  padding: 0 0;
  box-shadow: black 1vw 1vh 4vh;
}

p3.post {
  margin: 1vw 1vh;
  padding: 1vw 1vh;
  position: relative;
}

h1.title {
  padding: 0 0;
  margin: 0 0;
  left:10vw;
}

img {
  display: inline;
  position: relative;
  padding: 0vh 0vh 5vh;
  margin: 0 0;
}
img.bg {
  top: 0vh;
  left: 10vw;
  width: 80vw;
  height: 25vh;
}